/*ABOUT ME*/
html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0;
  background-color: #fafafa;
  scroll-behavior: smooth;
}

html, body {

	margin: 0;

	padding: 0;

	height: 100%;

}



#header {

	position: relative;

	height: 50px;

	background: #02c7ea;

}



#content {

	min-height: 100%;

	margin: -50px 0 -50px 0;

}



#main {

	padding: 50px 0 50px 0;

} 



#footer {

	height: 50px;

	background: #02c7ea;

}


.loding {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border: 4px solid #fff;
  border-bottom: 4px solid transparent;
  border-radius: 50%;
  animation: animate 1s linear infinite;
}

@keyframes animate {
  0%{
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100%{
    transform: translate(-50%, -50%) rotate(360deg);
  }
}